.breed-selec-container {
  width: 600px;
  margin-bottom: 32px;
}

.breed-select-hidden {
  visibility: hidden;
  display: none;
}

.error-hidden {
  display: none;
}

.error {
  color: red;
  display: inline-block;
}

.loader-container {
  width: 600px;
  text-align: center;
}

.loader-hidden {
  display: none;
}

.loader {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 10px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 100px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

